.chart {
  display: flex;
  align-items: center;
}
.box {
  height: 20px;
  border: 1px solid #2f3542;
}
.box_inner {
  width: 100%;
  background-color: #353b4a;
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.box_value {
  border-radius: 50px;
  background-color: #fff;
  transition: all 0.3s ease-out;
}
